import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FormControl, Validators, FormGroup, ValidationErrors} from '@angular/forms';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Helpers } from '../../../../../helpers';
import { ScriptLoaderService } from '../../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../../animations/round-anticlock.animation';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import { ToastComponent } from '../../../../layouts/toast-notification/toast.component';
import 'rxjs/add/operator/filter' ;

const EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&â€™*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
const MOBILE_REGEX=/^[91]{2}-\d{10}$/;
const PIN_REGEX=/^\d{6}$/;
const NUM_REGAX=/[0-9]/;

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./jc-psf-days-add.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    	FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})

export class JcPsfDaysAddComponent  {
@HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
@ViewChild(ToastComponent) toast: ToastComponent;
 
jcPsfDaysForm: FormGroup;
psfdaysId = 0;  
ajaxData : JcPsfDaysData[];
jcPsfDays= new JcPsfDaysData();
public status:string;
public executives=[];
public categorys=[];
public psfexecutives=[];

msg = [];
bootstrapFromValues = [];

constructor(private _script: ScriptLoaderService, 	private http: HttpClient, private _router: Router, private route:ActivatedRoute) {
	this.route.queryParams.filter(params => params.psfdaysId).subscribe(params=>{
			if(params.psfdaysId != undefined){
				this.psfdaysId=params.psfdaysId; 
			}
		}); 
	console.log("psfdaysId=="+this.psfdaysId);
	if(this.psfdaysId == 0){
		this.status = "Add";
	}else{
		this.status = "Update";
		this.populateData();
		this.populateDropDownData();
	}
	}
	
    ngOnInit() {
		this.jcPsfDaysForm = new FormGroup({			
			'psfdaysExeType' : new FormControl('', [ Validators.required]),
			'psfdaysJccatId' : new FormControl(''),
			'psfdaysDaycount' : new FormControl('', [ Validators.required]),
			'psfdaysDesc' : new FormControl('', [ Validators.required]),
			'psfdaysEmpId' : new FormControl(''),
			'psfdaysScript' : new FormControl('', [ Validators.required]),
			'psfdaysActive' : new FormControl(''),
			'psfdaysEntryId' : new FormControl(''),
			'psfdaysEntryDate' : new FormControl(''),
			'psfdaysModifiedId' : new FormControl(''),
			'psfdaysModifiedDate' : new FormControl(''),
		});
	}
	  
	get psfdaysExeType(){ return this.jcPsfDaysForm.get('psfdaysExeType')};
	get psfdaysJccatId(){ return this.jcPsfDaysForm.get('psfdaysJccatId')};
	get psfdaysDaycount(){ return this.jcPsfDaysForm.get('psfdaysDaycount')};
	get psfdaysDesc(){ return this.jcPsfDaysForm.get('psfdaysDesc')};
	get psfdaysEmpId(){ return this.jcPsfDaysForm.get('psfdaysEmpId')};
	get psfdaysScript(){ return this.jcPsfDaysForm.get('psfdaysScript')};
	get psfdaysActive(){ return this.jcPsfDaysForm.get('psfdaysActive')};
	get psfdaysEntryId(){ return this.jcPsfDaysForm.get('psfdaysEntryId')};
	get psfdaysEntryDate(){ return this.jcPsfDaysForm.get('psfdaysEntryDate')};
	get psfdaysModifiedId(){ return this.jcPsfDaysForm.get('psfdaysModifiedId')};
	get psfdaysModifiedDate(){ return this.jcPsfDaysForm.get('psfdaysModifiedDate')};
	 
	populateData(){
			  	const headers = new HttpHeaders({'Content-Type':'application/json'});
			    this.http.post<JcPsfDaysData>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer/customer-data ',
							 			JSON.stringify({ 'psfdaysId' :this.psfdaysId}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.jcPsfDays = data['populatadata'][0];
																		console.log("psfdaysModifiedId=="+this.jcPsfDays.psfdaysModifiedId);
			  															},
															 (err) => {
																	/* this function is executed when there's an ERROR */
																	console.log("ERROR: "+err);
															 });
	 }
 
 	populateDropDownData(){
		   const headers = new HttpHeaders({'Content-Type':'application/json'});
		   this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/branch',
							 			JSON.stringify({ emp_id :'0'}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.executives = data['populateBranch']
			  															});
		
		   this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/contact',
							 			JSON.stringify({ emp_id :'0'}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.categorys = data['populateContactTitle']
			  															});
		
		   this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/contact',
							 			JSON.stringify({ emp_id :'0'}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.psfexecutives = data['populateContactTitle']
			  															});
//		
	 }
 
	   ngAfterViewInit(){ }	
	
 
		 toggleAnimation(){
			this._router.navigate(['portal/customer-group-list']);      //path should be changed
		 }

 
	  	onSubmit() {
			this.bootstrapFromValues = $('#m_form_1').serializeArray();

			for(var i=0;i<this.bootstrapFromValues.length;i++){
				this.jcPsfDays[this.bootstrapFromValues[i].name]=this.bootstrapFromValues[i].value;
			}
			if(!this.jcPsfDaysForm.invalid){
				  const headers = new HttpHeaders({'Content-Type':'application/json'});
			if(this.psfdaysId == 0){ 
				this.jcPsfDays['requestType'] = 'add';
			}else{
				this.jcPsfDays['requestType'] = 'update';
				this.jcPsfDays['psfdaysId'] = this.psfdaysId+'';
			}
			 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/service/jcPsfDays',
									JSON.stringify(this.jcPsfDays),
						{ headers, withCredentials: true }).subscribe(data => {
															if(data['successmsg'] == undefined){
																this.msg[0]='Error';
																this.msg[1]=data['errormsg'];
															}else{
																if(data['successmsg'].includes('Successfully')){
																	this.msg[0]='success';
																	this.msg[1]=data['successmsg'];
																}
															}
	//			  											this.toast.showtoast(this.msg);
															if(this.msg[0]!='Error'){
																this.jcPsfDaysForm.reset();
																this.toggleAnimation();
															}			 
														},
														err =>{
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
//			 												this.toast.showtoast(this.msg);
		 												});
		  } 
			console.log(this.jcPsfDays);
	  }
 
  onDelete(){
		 this.jcPsfDays['requestType'] = 'delete';
		this.jcPsfDays['psfdaysId'] = this.psfdaysId+'';
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/service/jcPsfDays',
									JSON.stringify(this.jcPsfDays),
						{ headers, withCredentials: true }).subscribe(data => {			 											
			 											if(data['successmsg'].includes('Successfully')){
															this.msg[0]='success';
															this.msg[1]=data['successmsg'];
														}else{
															this.msg[0]='Error';
															this.msg[1]=data['successmsg'];
														}
//			 											this.toast.showtoast(this.msg);
			 											this.toggleAnimation();
			 											this.jcPsfDaysForm.reset();
//			 											this.list.getData();
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
//			 												this.toast.showtoast(this.msg);
		 												});
	 }
	  	
	
}
export class  JcPsfDaysData {
	psfdaysId:string;
	psfdaysJccatId:string;
	psfdaysExeType:string;
	psfdaysEmpId:string;
	psfdaysDaycount:string;
	psfdaysDesc:string;
	psfdaysScript:string;
	psfdaysActive:string="true";
	psfdaysContactableEmailEnable:string;
	psfdaysContactableEmailSub:string;
	psfdaysContactableEmailFormat:string;
	psfdaysContactableEmailExeSub:string;
	psfdaysContactableEmailExeFormat:string;
	psfdaysContactableSmsEnable:string="true";
	psfdaysContactableSmsFormat:string;
	psfdaysContactableSmsExeFormat:string;
	psfdaysNoncontactableEmailEnable:string="true";
	psfdaysNoncontactableEmailSub:string;
	psfdaysNoncontactableEmailFormat:string;
	psfdaysNoncontactableEmailExeSub:string;
	psfdaysNoncontactableEmailExeFormat:string;
	psfdaysNoncontactableSmsEnable:string="true";
	psfdaysNoncontactableSmsFormat:string;
	psfdaysNoncontactableSmsExeFormat:string;
	psfdaysSatisfiedEmailEnable:string="true";
	psfdaysSatisfiedEmailSub:string;
	psfdaysSatisfiedEmailFormat:string;
	psfdaysSatisfiedEmailExeSub:string;
	psfdaysSatisfiedEmailExeFormat:string;
	psfdaysSatisfiedSmsEnable:string="true";
	psfdaysSatisfiedSmsFormat:string;
	psfdaysSatisfiedSmsExeFormat:string;
	psfdaysDissatisfiedEmailEnable:string="true";
	psfdaysDissatisfiedEmailSub:string;
	psfdaysDissatisfiedEmailFormat:string;
	psfdaysDissatisfiedEmailExeSub:string;
	psfdaysDissatisfiedEmailExeFormat:string;
	psfdaysDissatisfiedSmsEnable:string="true";
	psfdaysDissatisfiedSmsFormat:string;
	psfdaysDissatisfiedSmsExeFormat:string;
	psfdaysEntryId:string;
	psfdaysEntryDate: string;
	psfdaysModifiedId: string;
	psfdaysModifiedDate: string;
  
}


